.overlayer {
  display: block;
  position: absolute;
  z-index: 101;
  background-image: linear-gradient(transparent 0px, black 100%);
  opacity: 1; }
  .overlayer.fullwidth {
    width: 100%; }
  .overlayer .overlayer-wrapper {
    display: block;
    position: relative;
    z-index: 100; }

.bottom-left {
  bottom: 0;
